/**
 * „Éó„É≠„Ç∞„É¨„ÉÉ„Ç∑„Éñ„Éª„Ç™„Éº„Éê„Éº„É≠„Éº„ÉâËøΩË∑°„Çµ„Éº„Éì„Çπ
 * ÈáçÈáè„ÉªÂõûÊï∞„ÅÆÊé®Áßª„ÄÅ1RMË®àÁÆó„ÄÅÁõÆÊ®ôË®≠ÂÆö„ÉªÈÅîÊàêÂ∫¶ÁÆ°ÁêÜ„ÇíË°å„ÅÜ
 */

import { supabaseService } from './supabaseService.js';
import { handleError } from '../utils/errorHandler.js';

class ProgressTrackingService {
    constructor() {
        this.supabase = supabaseService.client;
    }

    /**
   * 1RMÔºàÊúÄÂ§ßÊåô‰∏äÈáçÈáèÔºâ„ÇíË®àÁÆó„Åô„Çã
   * BrzyckiÂºè„Çí‰ΩøÁî®: 1RM = weight √ó (36 / (37 - reps))
   * @param {number} weight - ÈáçÈáèÔºàkgÔºâ
   * @param {number} reps - ÂõûÊï∞
   * @returns {number} 1RMÂÄ§
   */
    calculateOneRM(weight, reps) {
        try {
            if (!weight || !reps || weight <= 0 || reps <= 0) {
                throw new Error('ÈáçÈáè„Å®ÂõûÊï∞„ÅØÊ≠£„ÅÆÊï∞„Åß„ÅÇ„ÇãÂøÖË¶Å„Åå„ÅÇ„Çä„Åæ„Åô');
            }

            if (reps === 1) {
                return weight;
            }

            if (reps > 36) {
                throw new Error('36Âõû„ÇíË∂Ö„Åà„ÇãÂõûÊï∞„Åß„ÅØ1RMË®àÁÆó„Åå‰∏çÊ≠£Á¢∫„Å´„Å™„Çä„Åæ„Åô');
            }

            // BrzyckiÂºè„Å´„Çà„Çã1RMË®àÁÆó
            const oneRM = weight * (36 / (37 - reps));
            return Math.round(oneRM * 10) / 10; // Â∞èÊï∞ÁÇπÁ¨¨1‰Ωç„Åæ„Åß
        } catch (error) {
            handleError(error, { context: 'ProgressTrackingService.calculateOneRM' });
            return 0;
        }
    }

    /**
   * „Ç®„ÇØ„Çµ„Çµ„Ç§„Ç∫„ÅÆÈÄ≤Êçó„Éá„Éº„Çø„Çí‰øùÂ≠ò
   * @param {Object} progressData - ÈÄ≤Êçó„Éá„Éº„Çø
   * @returns {Promise<Object>} ‰øùÂ≠òÁµêÊûú
   */
    async saveProgressData(progressData) {
        try {
            const { data, error } = await this.supabase.from('training_logs').insert({
                user_id: progressData.userId,
                exercise_id: progressData.exerciseId,
                exercise_name: progressData.exerciseName,
                muscle_group_id: progressData.muscleGroupId,
                workout_date: progressData.workoutDate,
                sets: progressData.sets,
                reps: progressData.reps,
                weights: progressData.weights,
                workout_session_id: progressData.workoutSessionId,
                // 1RMË®àÁÆóÁµêÊûú„Çí‰øùÂ≠ò
                one_rm: this.calculateBestOneRM(
                    progressData.reps,
                    progressData.weights
                ),
                notes: progressData.notes
            });

            if (error) {
                throw error;
            }

            // ÈÄ≤ÊçóÁµ±Ë®à„ÇíÊõ¥Êñ∞
            await this.updateProgressStats(
                progressData.userId,
                progressData.exerciseId
            );

            return { success: true, data };
        } catch (error) {
            handleError(error, {
                context: 'ProgressTrackingService.saveProgressData'
            });
            return { success: false, error: error.message };
        }
    }

    /**
   * „Çª„ÉÉ„ÉàÂÜÖ„ÅßÊúÄÈ´ò„ÅÆ1RM„ÇíË®àÁÆó
   * @param {Array} repsArray - ÂõûÊï∞„ÅÆÈÖçÂàó
   * @param {Array} weightsArray - ÈáçÈáè„ÅÆÈÖçÂàó
   * @returns {number} ÊúÄÈ´ò1RMÂÄ§
   */
    calculateBestOneRM(repsArray, weightsArray) {
        try {
            let bestOneRM = 0;

            for (let i = 0; i < repsArray.length && i < weightsArray.length; i++) {
                const oneRM = this.calculateOneRM(weightsArray[i], repsArray[i]);
                if (oneRM > bestOneRM) {
                    bestOneRM = oneRM;
                }
            }

            return bestOneRM;
        } catch (error) {
            handleError(error, {
                context: 'ProgressTrackingService.calculateBestOneRM'
            });
            return 0;
        }
    }

    /**
   * „Ç®„ÇØ„Çµ„Çµ„Ç§„Ç∫„ÅÆÈÄ≤ÊçóÂ±•Ê≠¥„ÇíÂèñÂæó
   * @param {string} userId - „É¶„Éº„Ç∂„ÉºID
   * @param {string} exerciseId - „Ç®„ÇØ„Çµ„Çµ„Ç§„Ç∫ID
   * @param {number} days - ÂèñÂæó„Åô„ÇãÊó•Êï∞Ôºà„Éá„Éï„Ç©„É´„Éà: 90Êó•Ôºâ
   * @returns {Promise<Array>} ÈÄ≤ÊçóÂ±•Ê≠¥
   */
    async getProgressHistory(userId, exerciseId, days = 90) {
        try {
            const startDate = new Date();
            startDate.setDate(startDate.getDate() - days);

            const { data, error } = await this.supabase
                .from('training_logs')
                .select(
                    `
                    id,
                    workout_date,
                    sets,
                    reps,
                    weights,
                    one_rm,
                    exercise_name,
                    notes
                `
                )
                .eq('user_id', userId)
                .eq('exercise_id', exerciseId)
                .gte('workout_date', startDate.toISOString().split('T')[0])
                .order('workout_date', { ascending: true });

            if (error) {
                throw error;
            }

            // ÂêÑË®òÈå≤„ÅÆ1RM„ÇíÂÜçË®àÁÆóÔºà„Éá„Éº„Çø„Éô„Éº„Çπ„Å´‰øùÂ≠ò„Åï„Çå„Å¶„ÅÑ„Å™„ÅÑÂ†¥ÂêàÔºâ
            return data.map((record) => ({
                ...record,
                one_rm:
          record.one_rm || this.calculateBestOneRM(record.reps, record.weights)
            }));
        } catch (error) {
            handleError(error, {
                context: 'ProgressTrackingService.getProgressHistory'
            });
            return [];
        }
    }

    /**
   * ÈÄ≤ÊçóÁµ±Ë®à„ÇíÊõ¥Êñ∞
   * @param {string} userId - „É¶„Éº„Ç∂„ÉºID
   * @param {string} exerciseId - „Ç®„ÇØ„Çµ„Çµ„Ç§„Ç∫ID
   */
    async updateProgressStats(userId, exerciseId) {
        try {
            // ÊúÄÊñ∞30Êó•„ÅÆÈÄ≤Êçó„Éá„Éº„Çø„ÇíÂèñÂæó
            const history = await this.getProgressHistory(userId, exerciseId, 30);

            if (history.length === 0) {
                return;
            }

            // Áµ±Ë®àË®àÁÆó
            const oneRMValues = history.map((h) => h.one_rm).filter((rm) => rm > 0);
            const maxOneRM = Math.max(...oneRMValues);
            const avgOneRM =
        oneRMValues.reduce((sum, rm) => sum + rm, 0) / oneRMValues.length;

            // ÈÄ≤ÊçóÁéáË®àÁÆóÔºàÊúÄÂàù„Å®ÊúÄÂæå„ÅÆ1RM„ÇíÊØîËºÉÔºâ
            const firstOneRM = oneRMValues[0] || 0;
            const lastOneRM = oneRMValues[oneRMValues.length - 1] || 0;
            const progressRate =
        firstOneRM > 0 ? ((lastOneRM - firstOneRM) / firstOneRM) * 100 : 0;

            // Áµ±Ë®à„Éá„Éº„Çø„Çí‰øùÂ≠ò/Êõ¥Êñ∞
            const { error } = await this.supabase.from('progress_stats').upsert({
                user_id: userId,
                exercise_id: exerciseId,
                max_one_rm: maxOneRM,
                avg_one_rm: Math.round(avgOneRM * 10) / 10,
                progress_rate: Math.round(progressRate * 10) / 10,
                total_sessions: history.length,
                last_updated: new Date().toISOString()
            });

            if (error) {
                throw error;
            }
        } catch (error) {
            handleError(error, {
                context: 'ProgressTrackingService.updateProgressStats'
            });
        }
    }

    /**
   * ÁõÆÊ®ô„ÇíË®≠ÂÆö
   * @param {Object} goalData - ÁõÆÊ®ô„Éá„Éº„Çø
   * @returns {Promise<Object>} Ë®≠ÂÆöÁµêÊûú
   */
    async setGoal(goalData) {
        try {
            const goalRecord = {
                user_id: goalData.userId,
                exercise_id: goalData.exerciseId,
                goal_type: goalData.goalType, // 'weight', 'reps', 'one_rm'
                target_value: goalData.targetValue,
                current_value: goalData.currentValue,
                target_date: goalData.targetDate,
                description: goalData.description,
                priority: goalData.priority || 'medium',
                strategy: goalData.strategy || null,
                is_active: true,
                created_at: new Date().toISOString()
            };

            // ÈÄöÁü•Ë®≠ÂÆö„Åå„ÅÇ„ÇãÂ†¥Âêà„ÅØËøΩÂä†
            if (goalData.notifications) {
                goalRecord.notifications = JSON.stringify(goalData.notifications);
            }

            const { data, error } = await this.supabase
                .from('user_goals')
                .upsert(goalRecord);

            if (error) {
                throw error;
            }

            // ÁõÆÊ®ôË®≠ÂÆöÈÄöÁü•„ÇíÈÄÅ‰ø°
            if (goalData.notifications?.progress) {
                await this.scheduleGoalNotifications(goalData);
            }

            return { success: true, data };
        } catch (error) {
            handleError(error, { context: 'ProgressTrackingService.setGoal' });
            return { success: false, error: error.message };
        }
    }

    /**
   * ÁõÆÊ®ôÈÄöÁü•„Çí„Çπ„Ç±„Ç∏„É•„Éº„É´
   * @param {Object} goalData - ÁõÆÊ®ô„Éá„Éº„Çø
   */
    async scheduleGoalNotifications(goalData) {
        try {
            // „Éñ„É©„Ç¶„Ç∂ÈÄöÁü•„ÅÆË®±ÂèØ„ÇíË¶ÅÊ±Ç
            if ('Notification' in window && Notification.permission === 'default') {
                await Notification.requestPermission();
            }

            // ÊúüÈôêÂâç„É™„Éû„Ç§„É≥„ÉÄ„Éº„ÅÆË®≠ÂÆö
            if (goalData.notifications?.deadline) {
                const targetDate = new Date(goalData.targetDate);
                const reminderDate = new Date(targetDate);
                reminderDate.setDate(targetDate.getDate() - 7); // 1ÈÄ±ÈñìÂâç

                if (reminderDate > new Date()) {
                    // LocalStorage„Å´ÈÄöÁü•„Çπ„Ç±„Ç∏„É•„Éº„É´„Çí‰øùÂ≠ò
                    const notifications = JSON.parse(
                        localStorage.getItem('goalNotifications') || '[]'
                    );
                    notifications.push({
                        goalId: `${goalData.userId}_${goalData.exerciseId}_${goalData.goalType}`,
                        type: 'deadline_reminder',
                        scheduledDate: reminderDate.toISOString(),
                        message: `ÁõÆÊ®ô„Äå${goalData.description}„Äç„ÅÆÊúüÈôê„Åå1ÈÄ±ÈñìÂæå„Å´Ëø´„Å£„Å¶„ÅÑ„Åæ„Åô`,
                        goalData
                    });
                    localStorage.setItem(
                        'goalNotifications',
                        JSON.stringify(notifications)
                    );
                }
            }
        } catch (error) {
            handleError(error, {
                context: 'ProgressTrackingService.scheduleGoalNotifications'
            });
        }
    }

    /**
   * ÁõÆÊ®ô„ÅÆÈÄ≤Êçó„ÇíÁ¢∫Ë™ç„Åó„ÄÅÈÄöÁü•„ÇíÈÄÅ‰ø°
   * @param {string} userId - „É¶„Éº„Ç∂„ÉºID
   * @param {string} exerciseId - „Ç®„ÇØ„Çµ„Çµ„Ç§„Ç∫ID
   * @param {Object} _newRecord - Êñ∞„Åó„ÅÑË®òÈå≤ÔºàÂ∞ÜÊù•„ÅÆÊã°ÂºµÁî®Ôºâ
   */
    async checkGoalProgress(userId, exerciseId, _newRecord) {
        try {
            const goalProgress = await this.calculateGoalProgress(userId, exerciseId);

            if (!goalProgress.hasGoals || !goalProgress.progress) {
                return;
            }

            for (const goal of goalProgress.progress) {
                const notifications = goal.notifications
                    ? JSON.parse(goal.notifications)
                    : {};

                // „Éû„Ç§„É´„Çπ„Éà„Éº„É≥ÈÄöÁü•Ôºà25%, 50%, 75%, 90%ÈÅîÊàêÊôÇÔºâ
                if (notifications.milestone) {
                    const milestones = [25, 50, 75, 90];
                    const currentMilestone = milestones.find(
                        (m) =>
                            goal.progress_percentage >= m && goal.progress_percentage < m + 5 // 5%„ÅÆÁØÑÂõ≤ÂÜÖ
                    );

                    if (currentMilestone) {
                        this.sendGoalNotification(
                            'üéØ „Éû„Ç§„É´„Çπ„Éà„Éº„É≥ÈÅîÊàêÔºÅ',
                            `ÁõÆÊ®ô„Äå${goal.description}„Äç„ÅÆ${currentMilestone}%„ÇíÈÅîÊàê„Åó„Åæ„Åó„ÅüÔºÅ`
                        );
                    }
                }

                // ÁõÆÊ®ôÈÅîÊàêÈÄöÁü•
                if (goal.is_achieved && notifications.progress) {
                    this.sendGoalNotification(
                        'üéâ ÁõÆÊ®ôÈÅîÊàêÔºÅ',
                        `„Åä„ÇÅ„Åß„Å®„ÅÜ„Åî„Åñ„ÅÑ„Åæ„ÅôÔºÅÁõÆÊ®ô„Äå${goal.description}„Äç„ÇíÈÅîÊàê„Åó„Åæ„Åó„ÅüÔºÅ`
                    );

                    // ÈÅîÊàêÊ∏à„ÅøÁõÆÊ®ô„ÇíÈùû„Ç¢„ÇØ„ÉÜ„Ç£„ÉñÂåñ
                    await this.deactivateGoal(goal.id);
                }
            }
        } catch (error) {
            handleError(error, {
                context: 'ProgressTrackingService.checkGoalProgress'
            });
        }
    }

    /**
   * ÁõÆÊ®ôÈÄöÁü•„ÇíÈÄÅ‰ø°
   * @param {string} title - ÈÄöÁü•„Çø„Ç§„Éà„É´
   * @param {string} message - ÈÄöÁü•„É°„ÉÉ„Çª„Éº„Ç∏
   */
    sendGoalNotification(title, message) {
        try {
            // „Éñ„É©„Ç¶„Ç∂ÈÄöÁü•
            if ('Notification' in window && Notification.permission === 'granted') {
                new Notification(title, {
                    body: message,
                    icon: '/favicon-32x32.png',
                    tag: 'goal-progress'
                });
            }

            // „Ç¢„Éó„É™ÂÜÖÈÄöÁü•
            window.dispatchEvent(
                new CustomEvent('showNotification', {
                    detail: { message: `${title}\n${message}`, type: 'success' }
                })
            );
        } catch (error) {
            handleError(error, {
                context: 'ProgressTrackingService.sendGoalNotification'
            });
        }
    }

    /**
   * ÁõÆÊ®ô„ÇíÈùû„Ç¢„ÇØ„ÉÜ„Ç£„ÉñÂåñ
   * @param {string} goalId - ÁõÆÊ®ôID
   */
    async deactivateGoal(goalId) {
        try {
            const { error } = await this.supabase
                .from('user_goals')
                .update({ is_active: false, completed_at: new Date().toISOString() })
                .eq('id', goalId);

            if (error) {
                throw error;
            }
        } catch (error) {
            handleError(error, { context: 'ProgressTrackingService.deactivateGoal' });
        }
    }

    /**
   * ÁõÆÊ®ô„ÅÆÈÅîÊàêÂ∫¶„ÇíË®àÁÆó
   * @param {string} userId - „É¶„Éº„Ç∂„ÉºID
   * @param {string} exerciseId - „Ç®„ÇØ„Çµ„Çµ„Ç§„Ç∫ID
   * @returns {Promise<Object>} ÈÅîÊàêÂ∫¶„Éá„Éº„Çø
   */
    async calculateGoalProgress(userId, exerciseId) {
        try {
            // „Ç¢„ÇØ„ÉÜ„Ç£„Éñ„Å™ÁõÆÊ®ô„ÇíÂèñÂæó
            const { data: goals, error: goalError } = await this.supabase
                .from('user_goals')
                .select('*')
                .eq('user_id', userId)
                .eq('exercise_id', exerciseId)
                .eq('is_active', true);

            if (goalError) {
                throw goalError;
            }

            if (!goals || goals.length === 0) {
                return { hasGoals: false };
            }

            // ÊúÄÊñ∞„ÅÆÈÄ≤Êçó„Éá„Éº„Çø„ÇíÂèñÂæó
            const history = await this.getProgressHistory(userId, exerciseId, 7);

            if (history.length === 0) {
                return { hasGoals: true, goals, progress: [] };
            }

            const latestRecord = history[history.length - 1];

            // ÂêÑÁõÆÊ®ô„ÅÆÈÅîÊàêÂ∫¶„ÇíË®àÁÆó
            const progressData = goals.map((goal) => {
                let currentValue = 0;

                switch (goal.goal_type) {
                    case 'weight':
                        currentValue = Math.max(...latestRecord.weights);
                        break;
                    case 'reps':
                        currentValue = Math.max(...latestRecord.reps);
                        break;
                    case 'one_rm':
                        currentValue = latestRecord.one_rm;
                        break;
                }

                const progressPercentage =
          goal.target_value > 0
              ? Math.min(100, (currentValue / goal.target_value) * 100)
              : 0;

                return {
                    ...goal,
                    current_value: currentValue,
                    progress_percentage: Math.round(progressPercentage * 10) / 10,
                    is_achieved: progressPercentage >= 100
                };
            });

            return { hasGoals: true, goals, progress: progressData };
        } catch (error) {
            handleError(error, {
                context: 'ProgressTrackingService.calculateGoalProgress'
            });
            return { hasGoals: false, error: error.message };
        }
    }

    /**
   * ÊúàÈñì„Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÂàÜÊûê„ÇíÁîüÊàê
   * @param {string} userId - „É¶„Éº„Ç∂„ÉºID
   * @param {string} exerciseId - „Ç®„ÇØ„Çµ„Çµ„Ç§„Ç∫ID
   * @returns {Promise<Object>} ÂàÜÊûê„Éá„Éº„Çø
   */
    async generateMonthlyAnalysis(userId, exerciseId) {
        try {
            const history = await this.getProgressHistory(userId, exerciseId, 30);

            if (history.length === 0) {
                return { hasData: false };
            }

            // ÈÄ±Âà•„Éá„Éº„Çø„ÇíÈõÜË®à
            const weeklyData = this.groupByWeek(history);

            // „Éà„É¨„É≥„ÉâÂàÜÊûê
            const trend = this.analyzeTrend(history);

            // Áµ±Ë®àË®àÁÆó
            const stats = this.calculateStats(history);

            return {
                hasData: true,
                weeklyData,
                trend,
                stats,
                totalSessions: history.length,
                dateRange: {
                    start: history[0].workout_date,
                    end: history[history.length - 1].workout_date
                }
            };
        } catch (error) {
            handleError(error, {
                context: 'ProgressTrackingService.generateMonthlyAnalysis'
            });
            return { hasData: false, error: error.message };
        }
    }

    /**
   * „Éá„Éº„Çø„ÇíÈÄ±Âà•„Å´„Ç∞„É´„Éº„ÉóÂåñ
   * @param {Array} history - Â±•Ê≠¥„Éá„Éº„Çø
   * @returns {Array} ÈÄ±Âà•„Éá„Éº„Çø
   */
    groupByWeek(history) {
        const weeks = {};

        history.forEach((record) => {
            const date = new Date(record.workout_date);
            const weekStart = new Date(date);
            weekStart.setDate(date.getDate() - date.getDay()); // ÈÄ±„ÅÆÈñãÂßã„ÇíÊó•ÊõúÊó•„Å´Ë®≠ÂÆö
            const weekKey = weekStart.toISOString().split('T')[0];

            if (!weeks[weekKey]) {
                weeks[weekKey] = {
                    weekStart: weekKey,
                    sessions: [],
                    maxWeight: 0,
                    maxOneRM: 0,
                    totalVolume: 0
                };
            }

            weeks[weekKey].sessions.push(record);
            weeks[weekKey].maxWeight = Math.max(
                weeks[weekKey].maxWeight,
                Math.max(...record.weights)
            );
            weeks[weekKey].maxOneRM = Math.max(
                weeks[weekKey].maxOneRM,
                record.one_rm
            );

            // „Éú„É™„É•„Éº„É†Ë®àÁÆóÔºàÈáçÈáè √ó ÂõûÊï∞ √ó „Çª„ÉÉ„ÉàÊï∞Ôºâ
            const sessionVolume = record.weights.reduce((sum, weight, index) => {
                return sum + weight * record.reps[index];
            }, 0);
            weeks[weekKey].totalVolume += sessionVolume;
        });

        return Object.values(weeks).sort(
            (a, b) => new Date(a.weekStart) - new Date(b.weekStart)
        );
    }

    /**
   * „Éà„É¨„É≥„ÉâÂàÜÊûê„ÇíÂÆüË°å
   * @param {Array} history - Â±•Ê≠¥„Éá„Éº„Çø
   * @returns {Object} „Éà„É¨„É≥„ÉâÂàÜÊûêÁµêÊûú
   */
    analyzeTrend(history) {
        if (history.length < 2) {
            return { direction: 'insufficient_data', strength: 0 };
        }

        const oneRMValues = history.map((h) => h.one_rm).filter((rm) => rm > 0);

        if (oneRMValues.length < 2) {
            return { direction: 'insufficient_data', strength: 0 };
        }

        // Á∑öÂΩ¢ÂõûÂ∏∞„Å´„Çà„ÇãÂÇæÂêëÂàÜÊûê
        const n = oneRMValues.length;
        const x = Array.from({ length: n }, (_, i) => i);
        const y = oneRMValues;

        const sumX = x.reduce((sum, val) => sum + val, 0);
        const sumY = y.reduce((sum, val) => sum + val, 0);
        const sumXY = x.reduce((sum, val, i) => sum + val * y[i], 0);
        const sumXX = x.reduce((sum, val) => sum + val * val, 0);

        const slope = (n * sumXY - sumX * sumY) / (n * sumXX - sumX * sumX);

        let direction = 'stable';
        const strength = Math.abs(slope);

        if (slope > 0.1) {
            direction = 'improving';
        } else if (slope < -0.1) {
            direction = 'declining';
        }

        return { direction, strength: Math.round(strength * 100) / 100 };
    }

    /**
   * Áµ±Ë®à„Éá„Éº„Çø„ÇíË®àÁÆó
   * @param {Array} history - Â±•Ê≠¥„Éá„Éº„Çø
   * @returns {Object} Áµ±Ë®à„Éá„Éº„Çø
   */
    calculateStats(history) {
        const oneRMValues = history.map((h) => h.one_rm).filter((rm) => rm > 0);
        const weights = history.flatMap((h) => h.weights);
        const reps = history.flatMap((h) => h.reps);

        return {
            maxOneRM: Math.max(...oneRMValues),
            avgOneRM:
        Math.round(
            (oneRMValues.reduce((sum, rm) => sum + rm, 0) / oneRMValues.length) *
            10
        ) / 10,
            maxWeight: Math.max(...weights),
            avgWeight:
        Math.round(
            (weights.reduce((sum, w) => sum + w, 0) / weights.length) * 10
        ) / 10,
            maxReps: Math.max(...reps),
            avgReps:
        Math.round((reps.reduce((sum, r) => sum + r, 0) / reps.length) * 10) /
        10,
            improvement:
        oneRMValues.length > 1
            ? Math.round(
                ((oneRMValues[oneRMValues.length - 1] - oneRMValues[0]) /
                oneRMValues[0]) *
                100 *
                10
            ) / 10
            : 0
        };
    }
}

